































import Vue from 'vue';

export default Vue.extend({
    data() {
        return {
            prefix: 'https://',
            idp: '',
        };
    },
    methods: {
        loginWithSolid() {
            this.$ui.wrapAsyncOperation(
                this.$auth.loginWithSolid(this.prefix + this.idp),
                'Logging in...',
            );
        },
        loginOffline() {
            this.$ui.wrapAsyncOperation(
                this.$auth.loginOffline(),
                'Logging in...',
            );
        },
    },
});
