













































import Vue from 'vue';

import Task from '@/models/Task';
import List from '@/models/List';

import TaskItem from '@/components/TaskItem.vue';
import VerticalSlide from '@/components/transitions/VerticalSlide.vue';

interface Data {
    newTask: string;
    showCompleted: boolean;
}

export default Vue.extend({
    components: {
        TaskItem,
        VerticalSlide,
    },
    props: {
        list: {
            type: Object as () => List,
            required: true,
        },
    },
    data(): Data {
        return {
            newTask: '',
            showCompleted: false,
        };
    },
    computed: {
        pendingTasks(): Task[] {
            return this.list.tasks.filter((task: Task) => !task.completed);
        },
        completedTasks(): Task[] {
            return this.list.tasks.filter((task: Task) => task.completed);
        },
    },
    methods: {
        async create() {
            if (this.newTask) {
                const newTask = this.newTask;
                this.newTask = '';

                try {
                    await this.$workspaces.createTask(this.list, newTask);
                } catch (e) {
                    this.$ui.showError(e);

                    // TODO delete task from local store
                }
            }
        },
    },
});
