





















import Vue from 'vue';

import Task from '@/models/Task';

export default Vue.extend({
    props: {
        task: {
            type: Object as () => Task,
            required: true,
        },
    },
    methods: {
        toggle() {
            // Allow the checkbox to be displayed as checked before the animation starts
            this.$nextTick(async () => {
                try {
                    await this.$workspaces.toggleTask(this.task);
                } catch (e) {
                    this.$ui.showError(e);

                    // TODO revert task status
                }
            });
        },
    },
});
