






















import Vue from 'vue';

import { Dialog } from '@/services/UI';

export default Vue.extend({
    props: {
        title: {
            type: String,
            default: null,
        },
        dialog: {
            type: Object as () => Dialog,
            required: true,
        },
        color: {
            type: String,
            default: null,
        },
        persistent: {
            type: Boolean,
            default: false,
        },
        dark: {
            type: Boolean,
            default: false,
        },
    },
    methods: {
        close() {
            if (!this.persistent) {
                this.$ui.closeDialog(this.dialog.id);
            }
        },
    },
});
